import Dieline from './dieline'

class MailerBoxDieline extends Dieline {

  constructor(args) {
    super(args)
    this.style = "Mailer Box"
  }

  rectangles() {

    const firstRectangleX = this.length + 7 * this.thickness
    const firstRectangleY = this.depth + this.thickness / 2
    const firstFlapX = Math.min(...[
      this.depth,
      this.width / 2,
    ])

    const secondRectangleX = this.length + this.thickness
    const secondRectangleY = this.width + 2 * this.thickness
    const secondFlapX = this.depth + this.thickness / 2

    const thirdRectangleX = this.length + 10 * this.thickness
    const thirdRectangleY = this.depth + this.thickness
    const thirdFlapX = this.width / 2 - 2 * this.thickness

    const fourthRectangleX = this.length + 9 * this.thickness
    const fourthRectangleY = this.width + this.thickness
    const fourthFlapX = 2 * this.depth + 6.5 * this.thickness

    const fifthRectangleX = this.length + 5 * this.thickness
    const fifthRectangleY = this.depth + this.thickness / 2
    const fifthFlapX = this.width / 2 + this.thickness / 2

    return [
      {
        width: firstRectangleX + 2 * firstFlapX,
        length: firstRectangleY,
      },
      {
        width: secondRectangleX + 2 * secondFlapX,
        length: secondRectangleY,
      },
      {
        width: thirdRectangleX + 2 * thirdFlapX,
        length: thirdRectangleY,
      },
      {
        width: fourthRectangleX + 2 * fourthFlapX,
        length: fourthRectangleY,
      },
      {
        width: fifthRectangleX + 2 * fifthFlapX,
        length: fifthRectangleY,
      },
    ]
  }

}

export default MailerBoxDieline
